import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { CommonModule } from '@angular/common';
import { MembershipService } from '../../membership.service';
import { IBaseResponse } from '../../interfaces/base-response.interface';
import { User } from '../../interfaces/user.interface';
import { SharedModule } from '../../../shared/shared.module';
import { LoginConfig, UnifiedLoginComponent } from '../../../shared/components/unified-login/unified-login.component';

@Component({
  selector: 'app-login',
  template: '<app-unified-login #loginComponent [config]="loginConfig" (loginSubmit)="onLogin($event)"></app-unified-login>',
  standalone: true,
  imports: [
    CommonModule,
    SharedModule
  ]
})
export class LoginComponent implements OnInit, OnDestroy {
  @ViewChild('loginComponent') loginComponent?: UnifiedLoginComponent;

  loginConfig: LoginConfig = {
    title: '👥 멤버십',
    subtitle: '회원 관리 시스템',
    moduleName: 'membership',
    showSocialLogin: false,
    showRegisterLink: false,
    themeColors: {
      primary: '#6366f1',    // 인디고 (멤버십/사용자)
      secondary: '#4f46e5',  // 진한 인디고
      accent: '#818cf8'      // 밝은 인디고
    }
  };

  subscription?: Subscription;

  constructor(
    private router: Router,
    public membershipService: MembershipService
  ) {}

  ngOnInit(): void {
    // 초기화 로직이 필요하면 여기에 추가
  }

  ngOnDestroy(): void {
    this.subscription?.unsubscribe();
  }

  onLogin(credentials: { username: string; password: string }): void {
    const param = {
      id: credentials.username,
      password: credentials.password
    };

    if (this.loginComponent) {
      this.loginComponent.setLoading(true);
    }

    this.subscription = this.membershipService.login<IBaseResponse<any>>(param).subscribe({
      next: (res) => {
        console.log('로그인 성공:', res);

        if (!res.data.accessToken) {
          if (this.loginComponent) {
            this.loginComponent.setError('accessToken이 존재하지 않습니다.');
            this.loginComponent.setLoading(false);
          }
          return;
        }

        this.decodeAndSaveToken(res.data.accessToken);
      },
      error: (e) => {
        console.warn('로그인 실패:', e);
        if (this.loginComponent) {
          const errorMessage = e.error?.data?.message || '로그인에 실패했습니다.';
          this.loginComponent.setError(errorMessage);
          this.loginComponent.setLoading(false);
        }
      }
    });
  }

  decodeAndSaveToken(accessToken: string): void {
    const payload = JSON.parse(atob(accessToken.split('.')[1]));
    const user: User = {
      userIdx: payload.userIdx || payload.userId,
      id: payload.id || payload.userId,
      password: '',
      name: payload.name,
      department: payload.department || null,
      email: payload.email,
      phone: payload.phone || payload.phoneNumber || '',
      role: payload.role,
      joinDate: payload.joinDate ? new Date(payload.joinDate) : null,
      createdAt: payload.createdAt ? new Date(payload.createdAt) : undefined,
      isPasswordReset: payload.isPasswordReset || false,
      userPermission: payload.permissions || payload.userPermission || [],
      userOrganizations: payload.userOrganizations || []
    };

    this.membershipService.setToken(accessToken);
    this.membershipService.setUser(user);

    // localStorage에 userInfo 저장 (교육 모듈 설문 등에서 사용)
    localStorage.setItem('userInfo', JSON.stringify({
      id: user.id,
      name: user.name,
      email: user.email,
      role: user.role
    }));
    console.log('userInfo saved to localStorage:', { id: user.id, name: user.name });

    if (user.isPasswordReset) {
      console.log('비밀번호 변경이 필요합니다.');
      // TODO: 비밀번호 변경 다이얼로그 표시
      this.navigateToHome();
    } else {
      this.navigateToHome();
    }
  }

  navigateToHome(): void {
    this.router.navigate(['/membership/home']);
  }
}
