import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CommonModule } from '@angular/common';
import { MatButtonModule } from '@angular/material/button';
import { MatCardModule } from '@angular/material/card';
import { MatIconModule } from '@angular/material/icon';
import { MembershipService } from '../../membership.service';
import { User } from '../../interfaces/user.interface';
import { RoleId } from '../../util/role-id.enum';
import { MembershipHeaderComponent } from '../membership-header/membership-header.component';

@Component({
  selector: 'app-membership-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  standalone: true,
  imports: [
    CommonModule,
    MatButtonModule,
    MatCardModule,
    MatIconModule,
    MembershipHeaderComponent
  ]
})
export class MembershipHomeComponent implements OnInit {
  currentUser: User | null = null;

  constructor(
    private router: Router,
    public membershipService: MembershipService
  ) {}

  ngOnInit(): void {
    this.loadUserInfo();
  }

  loadUserInfo(): void {
    this.currentUser = this.membershipService.getUser();
    
    if (!this.currentUser) {
      // 로그인되지 않은 경우 로그인 페이지로 이동
      this.router.navigate(['/membership/login']);
    }
  }

  logout(): void {
    this.membershipService.logout();
    this.router.navigate(['/membership/login']);
  }

  goToProfile(): void {
    // TODO: 프로필 페이지 구현 후 연결
    console.log('프로필 페이지로 이동');
  }

  goToSettings(): void {
    // TODO: 설정 페이지 구현 후 연결
    console.log('설정 페이지로 이동');
  }

  goToUserList(): void {
    this.router.navigate(['/membership/users']);
  }

  isAdminOrAbove(): boolean {
    if (!this.currentUser || !this.currentUser.role) return false;
    return this.currentUser.role.roleId >= RoleId.Admin;
  }
}
