import { Component, OnInit, OnDestroy } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { MatTableModule } from '@angular/material/table';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatCardModule } from '@angular/material/card';
import { MatChipsModule } from '@angular/material/chips';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatTabsModule } from '@angular/material/tabs';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { MatSelectModule } from '@angular/material/select';
import { MatDividerModule } from '@angular/material/divider';
import { MatDialog, MatDialogModule } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { MembershipService } from '../../membership.service';
import { User } from '../../interfaces/user.interface';
import { RoleId } from '../../util/role-id.enum';
import { RoleName } from '../../util/role-name.enum';
import { MembershipHeaderComponent } from '../membership-header/membership-header.component';
import { UserEditDialogComponent } from '../user-edit-dialog/user-edit-dialog.component';
import { UpdateUserDto } from '../../dto/update-user.dto';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.scss'],
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    MatTableModule,
    MatButtonModule,
    MatIconModule,
    MatCardModule,
    MatChipsModule,
    MatTooltipModule,
    MatProgressSpinnerModule,
    MatTabsModule,
    MatFormFieldModule,
    MatInputModule,
    MatSelectModule,
    MatDividerModule,
    MatDialogModule,
    MembershipHeaderComponent
  ]
})
export class UserListComponent implements OnInit, OnDestroy {
  private destroy$ = new Subject<void>();
  
  // Enum을 템플릿에서 사용하기 위해 노출
  RoleId = RoleId;
  
  // UI 상태
  loading = true;
  isProcessing = false;
  selectedTabIndex = 0;
  
  // 사용자 데이터
  allUsers: User[] = [];
  displayedUsers: User[] = [];
  selectedRoleFilter: 'ALL' | RoleId = 'ALL';
  searchQuery = '';
  
  // 현재 로그인한 사용자
  currentUser: User | null = null;
  
  // 테이블 컬럼
  displayedColumns = ['name', 'id', 'email', 'role', 'organization', 'createdAt', 'actions'];
  
  // 역할 필터 옵션
  roleFilters = [
    { value: 'ALL', label: '전체' },
    { value: RoleId.User, label: '일반 사용자' },
    { value: RoleId.Admin, label: '관리자' },
    { value: RoleId.SuperAdmin, label: '슈퍼 관리자' }
  ];

  constructor(
    private membershipService: MembershipService,
    private router: Router,
    private dialog: MatDialog
  ) {}

  ngOnInit(): void {
    this.currentUser = this.membershipService.getUser();
    
    if (!this.currentUser) {
      this.router.navigate(['/membership/login']);
      return;
    }
    
    // 권한 체크: Admin 이상만 접근 가능
    if (!this.isAdminOrAbove()) {
      alert('관리자 권한이 필요합니다.');
      this.router.navigate(['/membership/home']);
      return;
    }
    
    this.loadUsers();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  /**
   * 전체 사용자 목록 로드
   */
  loadUsers(): void {
    this.loading = true;
    this.isProcessing = true;
    
    this.membershipService.getAllUsers<any>()
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (response) => {
          console.log('사용자 목록 로드 성공:', response);
          this.allUsers = response.data || [];
          this.filterUsers();
          this.loading = false;
          this.isProcessing = false;
        },
        error: (error) => {
          console.warn('사용자 목록 로드 실패:', error);
          this.loading = false;
          this.isProcessing = false;
          alert('사용자 목록을 불러오는데 실패했습니다.');
        }
      });
  }

  /**
   * 사용자 필터링
   */
  filterUsers(): void {
    let filtered = [...this.allUsers];
    
    // 역할 필터
    if (this.selectedRoleFilter !== 'ALL') {
      filtered = filtered.filter(user => user.role?.roleId === this.selectedRoleFilter);
    }
    
    // 검색 필터
    if (this.searchQuery.trim()) {
      const query = this.searchQuery.toLowerCase();
      filtered = filtered.filter(user => 
        user.name?.toLowerCase().includes(query) ||
        user.id?.toLowerCase().includes(query) ||
        user.email?.toLowerCase().includes(query)
      );
    }
    
    this.displayedUsers = filtered;
  }

  /**
   * 역할 필터 변경
   */
  onRoleFilterChange(roleId: 'ALL' | RoleId): void {
    this.selectedRoleFilter = roleId;
    this.filterUsers();
  }

  /**
   * 검색어 변경
   */
  onSearchChange(query: string): void {
    this.searchQuery = query;
    this.filterUsers();
  }

  /**
   * 헤더에서 검색
   */
  onSearch(query: string): void {
    this.searchQuery = query;
    this.filterUsers();
  }

  /**
   * 사용자 클릭 (상세/편집)
   */
  onUserClick(user: User): void {
    const dialogRef = this.dialog.open(UserEditDialogComponent, {
      width: '600px',
      maxWidth: '95vw',
      data: {
        user: user,
        isMe: this.isMe(user)
      },
      disableClose: false,
      autoFocus: true
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.updateUser(user.id, result);
      }
    });
  }

  /**
   * 사용자 정보 업데이트
   */
  updateUser(userId: string, updateData: { name: string; email: string; roleId: RoleId }): void {
    this.isProcessing = true;

    const updateDto: UpdateUserDto = {
      userId: userId,
      name: updateData.name,
      email: updateData.email,
      role: updateData.roleId
    };

    this.membershipService.updateUser<any>(updateDto)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (response: any) => {
          console.log('사용자 정보 업데이트 성공:', response);
          alert('사용자 정보가 성공적으로 업데이트되었습니다.');
          this.loadUsers(); // 목록 새로고침
        },
        error: (error: any) => {
          console.warn('사용자 정보 업데이트 실패:', error);
          alert('사용자 정보 업데이트에 실패했습니다.');
          this.isProcessing = false;
        }
      });
  }

  /**
   * 사용자 삭제
   */
  async onUserDelete(user: User): Promise<void> {
    if (this.isMe(user)) {
      alert('본인은 삭제할 수 없습니다.');
      return;
    }
    
    // confirmAsync 사용 (동적 import)
    const { confirmAsync } = await import('../../../../utils/alert-override.util');
    const confirmed = await confirmAsync(`${user.name || user.id} 사용자를 삭제하시겠습니까?`);
    
    if (!confirmed) {
      return;
    }
    
    this.isProcessing = true;
    this.membershipService.deleteUserById<any>(user.id)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: () => {
          console.log('사용자 삭제 성공');
          alert('사용자가 삭제되었습니다.');
          this.loadUsers();
        },
        error: (error) => {
          console.warn('사용자 삭제 실패:', error);
          this.isProcessing = false;
          alert('사용자 삭제에 실패했습니다.');
        }
      });
  }

  /**
   * 헬퍼 메서드
   */
  getRoleName(user: User): string {
    return user.role?.roleName || '알 수 없음';
  }

  getRoleClass(user: User): string {
    const roleId = user.role?.roleId;
    if (roleId === RoleId.SuperAdmin) return 'role-superadmin';
    if (roleId === RoleId.Admin) return 'role-admin';
    return 'role-user';
  }

  getOrganizationName(user: User): string {
    if (user.userOrganizations && user.userOrganizations.length > 0) {
      return user.userOrganizations[0].name;
    }
    return '-';
  }

  formatDate(date: any): string {
    if (!date) return '-';
    return new Date(date).toLocaleDateString('ko-KR');
  }

  isAdminOrAbove(): boolean {
    if (!this.currentUser || !this.currentUser.role) return false;
    return this.currentUser.role.roleId >= RoleId.Admin;
  }

  isMe(user: User): boolean {
    return this.currentUser?.id === user.id;
  }

  canEdit(user: User): boolean {
    if (!this.currentUser || !this.currentUser.role) return false;
    if (this.isMe(user)) return true;
    
    // 슈퍼 관리자는 모든 사용자 편집 가능
    if (this.currentUser.role.roleId === RoleId.SuperAdmin) return true;
    
    // 관리자는 일반 사용자만 편집 가능
    if (this.currentUser.role.roleId === RoleId.Admin) {
      return (user.role?.roleId || 0) < RoleId.Admin;
    }
    
    return false;
  }

  canDelete(user: User): boolean {
    if (this.isMe(user)) return false;
    return this.canEdit(user);
  }

  /**
   * 역할별 사용자 수 계산
   */
  getUserCountByRole(roleId: RoleId): number {
    return this.allUsers.filter(user => user.role?.roleId === roleId).length;
  }

  /**
   * 필터 아이콘 반환
   */
  getFilterIcon(value: any): string {
    if (value === 'ALL') return 'people';
    if (value === RoleId.SuperAdmin) return 'shield';
    if (value === RoleId.Admin) return 'admin_panel_settings';
    return 'person';
  }

  /**
   * 역할 아이콘 반환
   */
  getRoleIcon(user: User): string {
    const roleId = user.role?.roleId;
    if (roleId === RoleId.SuperAdmin) return 'shield';
    if (roleId === RoleId.Admin) return 'admin_panel_settings';
    return 'person';
  }
}
