import { Component, Input, Output, EventEmitter, OnInit, OnDestroy } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { MatTooltipModule } from '@angular/material/tooltip';
import { ThemeService } from '../../../../services/theme.service';

export interface MembershipHeaderAction {
  type: 'search' | 'menu' | 'toolbar';
  data: any;
}

@Component({
  selector: 'app-membership-header',
  templateUrl: './membership-header.component.html',
  styleUrls: ['./membership-header.component.scss'],
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    MatIconModule,
    MatButtonModule,
    MatTooltipModule
  ]
})
export class MembershipHeaderComponent implements OnInit, OnDestroy {
  private destroy$ = new Subject<void>();
  
  @Input() pageTitle = '멤버십';
  @Input() pageSubtitle = '';
  @Input() activeMenu = '홈';
  @Input() itemCount = 0;
  @Input() isSearchMode = false;
  @Input() isProcessing = false;
  @Input() showSearchBar = true;
  @Input() showRefreshButton = false;

  @Output() menuChange = new EventEmitter<string>();
  @Output() toolbarAction = new EventEmitter<MembershipHeaderAction>();
  @Output() search = new EventEmitter<string>();
  @Output() leftPanelToggle = new EventEmitter<void>();
  @Output() refresh = new EventEmitter<void>();

  searchQuery = '';
  isDarkMode = false;

  constructor(
    private themeService: ThemeService,
    private router: Router
  ) {}

  ngOnInit(): void {
    // 테마 상태 구독
    this.themeService.currentTheme$
      .pipe(takeUntil(this.destroy$))
      .subscribe(theme => {
        this.isDarkMode = theme === 'dark';
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  onLogoClick(): void {
    this.router.navigate(['/membership']);
  }

  toggleLeftPanel(): void {
    this.leftPanelToggle.emit();
  }

  onSearchSubmit(): void {
    if (this.searchQuery.trim()) {
      this.search.emit(this.searchQuery);
    }
  }

  onRefreshClick(): void {
    this.refresh.emit();
  }

  toggleTheme(): void {
    this.themeService.toggleTheme();
  }

  onMenuClick(menu: string): void {
    this.activeMenu = menu;
    this.menuChange.emit(menu);
  }
}
