import { Component, Inject, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormBuilder, FormGroup, Validators, ReactiveFormsModule } from '@angular/forms';
import { MatDialogModule, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatButtonModule } from '@angular/material/button';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { MatSelectModule } from '@angular/material/select';
import { MatIconModule } from '@angular/material/icon';
import { MatDividerModule } from '@angular/material/divider';
import { User } from '../../interfaces/user.interface';
import { RoleId } from '../../util/role-id.enum';
import { RoleName } from '../../util/role-name.enum';

export interface UserEditDialogData {
  user: User;
  isMe: boolean;
}

@Component({
  selector: 'app-user-edit-dialog',
  templateUrl: './user-edit-dialog.component.html',
  styleUrls: ['./user-edit-dialog.component.scss'],
  standalone: true,
  imports: [
    CommonModule,
    ReactiveFormsModule,
    MatDialogModule,
    MatButtonModule,
    MatFormFieldModule,
    MatInputModule,
    MatSelectModule,
    MatIconModule,
    MatDividerModule
  ]
})
export class UserEditDialogComponent implements OnInit {
  editForm: FormGroup;
  isMe: boolean;
  
  roleOptions = [
    { value: RoleId.User, label: RoleName.User },
    { value: RoleId.Admin, label: RoleName.Admin },
    { value: RoleId.SuperAdmin, label: RoleName.SuperAdmin }
  ];

  constructor(
    private fb: FormBuilder,
    public dialogRef: MatDialogRef<UserEditDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: UserEditDialogData
  ) {
    this.isMe = data.isMe;
    
    this.editForm = this.fb.group({
      name: [data.user.name || '', [Validators.required]],
      email: [data.user.email || '', [Validators.required, Validators.email]],
      roleId: [
        { value: data.user.role?.roleId || RoleId.User, disabled: this.isMe },
        [Validators.required]
      ]
    });
  }

  ngOnInit(): void {}

  onCancel(): void {
    this.dialogRef.close();
  }

  onSave(): void {
    if (this.editForm.valid) {
      const formValue = this.editForm.getRawValue(); // getRawValue로 disabled 필드도 가져옴
      this.dialogRef.close({
        name: formValue.name,
        email: formValue.email,
        roleId: formValue.roleId
      });
    }
  }

  getRoleLabel(roleId: RoleId): string {
    const role = this.roleOptions.find(r => r.value === roleId);
    return role ? role.label : '알 수 없음';
  }
}
